import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/Bachelor/image1.png"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Bachelor's Thesis</h1>
        
        <img class="bach" src={image1} alt="image1"/>

        <div>
          <p>
          A text generator that uses a modified Markov Chain. It takes as input books from a genre or multiple genres and creates a new paragraph based on user input.
          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage